.exhibitions {
  &__content {
    margin-bottom: 30px;

    @include onTablet {
      margin-bottom: 0;
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      column-gap: 30px;
    }

    @include onDesktop {
      padding-bottom: 90px;
    }
  }

  &__card {
    margin-bottom: 60px;

    @include onTablet {
      margin-bottom: 0;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }
}
